<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="type">
          <el-select
            v-model="query.type"
            filterable
            clearable
            class="filter-item"
            placeholder="单据类型"
            style="width: 178px;"
          >
            <el-option
              v-for="item in dict['wms_delivery_receipt_type']"
              :key="item.id"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="status">
          <el-select v-model="query.status" filterable clearable placeholder="状态" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in statusPullMap"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="billCreateUser">
          <el-input v-model.trim="query.billCreateUser" clearable placeholder="创建人" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="code">
          <el-input v-model.trim="query.code" clearable placeholder="合同号或采购订单号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="supplier">
          <el-input v-model.trim="query.supplier" clearable placeholder="供应商名称或编号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="no">
          <el-input v-model.trim="query.no" clearable placeholder="交接单号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="startDate">
          <el-date-picker
            v-model="query.startDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="开始时间"
            @change="query.endDate = ''"
          />
        </el-form-item>
        <el-form-item prop="endDate">
          <el-date-picker
            v-model="query.endDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="结束时间"
            :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startDate).getTime()}"
          />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="145"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handleDetail(scope.row)">详情</el-button>
          <!-- canStockInMatarialCount 可入库物料数量，等于0就置灰入库按钮 -->
          <el-button type="primary" :disabled="!([3, 4].includes(scope.row.status) && scope.row.canStockInMatarialCount > 0)" @click="handleInWarehouse(scope.row)">入库</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getDeleteApi } from '@/apis/modules/base/baseSupplier'
import { getListApi } from '@/apis/modules/in-storage/info'
import { infoStatusPullMap, statusMap, statusChartMap } from '@/views/in-storage/info/status'
import tableHeadSet from '@/mixins/tableHeadSet'

export default {
  mixins: [paginationPage, tableHeadSet],
  dicts: ['wms_delivery_receipt_type'],
  data() {
    return {
      title: '入库信息',
      loading: false,
      query: {
        type: '',
        status: '',
        billCreateUser: '',
        code: '',
        supplier: '',
        no: '',
        startDate: '',
        endDate: ''
      },
      tableData: [],
      columnSets: [
        {
          prop: 'no',
          width: 120
        },
        {
          module: 'status',
          prop: 'status',
          formatter: (status) => {
            return statusChartMap[status]
          },
          statusFormatter: (row) => {
            return statusMap[row.status]
          }
        },
        {
          module: 'status',
          prop: 'type_dictText',
          label: '单据类型',
          align: 'center',
          formatter: (prop) => {
            return prop
          },
          headerAlign: 'center',
          // <el-tag /> 标签type取值,参数为当前行数据
          statusFormatter: (scope) => {
            // 1表示订单2表示外协3表示寄售4.表示生产
            const typeMap = {
              '1': 'success',
              '2': 'primary',
              '3': 'warning',
              '4': 'info'
            }
            return scope.type ? typeMap[scope.type] : ''
          }
        }
      ],
      dialogForm: {},
      supplierPullList: [],
      dialogType: 'add',
      dialogVisible: false,
      statusPullMap: infoStatusPullMap
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.init()
  },
  activated() {
    this.init()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type ? { ...this.query } : {
        ...this.query,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 批量删除
    handleDelete(params, item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        console.log({
          id: params[0],
          dataSources: item.dataSources
        })
        const ids = params || this.$refs.baseTableRef.tableSelectionList.map(i => i.id)
        getDeleteApi({
          id: ids[0],
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // 详情
    handleDetail(item) {
      this.$router.push({
        name: 'InStorageInfoDetail',
        params: {
          id: item.id
        }
      })
    },
    // 入库
    handleInWarehouse(item) {
      this.$router.push({
        name: 'InStorageInfoAdd',
        params: {
          id: item.id // 交接单id
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
